.navbar-overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: $navbar-height;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    background-color: #fff;
    font-size: 1.2rem;
}

.brand {
    position: absolute;
    top:0; left:0;
    height: $navbar-height;
    display: flex;
    align-items: center;
    padding-left: 50px;
    padding-right: 10px;
    font-size: 1.6rem;
    margin-top: -0.15rem;
}

.navbar-overlay a {
    text-decoration: none;
    color: $secondary-color;
}

.navbar-nav {
    justify-content: flex-end;
    ul {
        padding: 0;
        margin: 0;
        display: flex;
        list-style: none;
        height: 100%;
        li {
            height: 100%;
            a {
                height: 100%;
                display: flex;
                align-items: center;
                padding: 0 2rem;
                
            }
        }
    }
}

/* s and xs devices*/
@media only screen and (max-width: 600px) {
    .navbar-nav {
        ul {
            li {
                a {
                    padding: 0 1rem;
                }
            }
        }
    }
    .navbar {
        padding: $navbar-padding-small;
    }
}