@font-face {
  font-family: "Avenir";
  src: url("assets/fonts/Avenir2.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Avenir";
  src: url("assets/fonts/Avenir0.ttf") format("truetype");
}

@font-face {
  font-family: "hindi";
  src: url("assets/fonts/ananda\ namaste.ttf") format("truetype");
}

@font-face {
  font-family: "serif";
  src: url("assets/fonts/Andada-Regular.otf") format("truetype");
}

body {
  margin: 0;
  font-size: 1.1rem;
  font-family: 'Avenir';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


$primary-color: #222222;
$secondary-color: #CF9F3B;



/*Navbar*/
$navbar-height: 60px;
$navbar-padding: 0 50px;
$navbar-padding-small: 0 25px;


/*Body*/
$container-padding: 4rem 12vw;
$default-border-radius: 5px;

/*Headers*/
h1 {
    text-align: center;
}
h3 {
    margin-top: 0;
}

body {
    color: $primary-color;
}

a {
    text-decoration: none;
    color: $primary-color;
}

p {
    margin: 0.5rem 0;
}

.MuiButton-root {
    text-transform: none!important;
}

h3, h4, h5, h6 {
    text-transform: uppercase;
}

section {
    padding: 2rem 0;
}
iframe {
    width: 100%;
}

img {
    border-radius: $default-border-radius;
}

video {
    border-radius: $default-border-radius;
}

.full-width {
    width: 100%;
}

.centered-item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.center-text {
     text-align: center;
}


/* s and xs devices*/
@media only screen and (max-width: 600px) {
    h1 {
        font-size: 1.2rem;
    }
    .hide-on-small {
        display: none;
    }
}